import React, {useState} from "react";


// components
import Offcanvas from "react-bootstrap/Offcanvas";
import SidebarItems from "./SidebarItems";

// img
import menu from "./menu.png";

// css
import classes from "./sidebar.module.css";
import "./main.css"

function Sidebar(props) {

    /////////////// states

    const [show_desktop, setShow_desktop] = useState(false);
    const [show, setShow] = useState(false);

    /////////////// functions

    const getBackHandler = (event, selected) => {
        props.getBack(event);
        if(selected.selected) {
            setShow(false);
        }
    }

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    return (
        <>
            {/* desktop */}
            
            <button 
                className={classes.sidebarBtn}
                onClick={()=> setShow_desktop(state => !state)}
            >
                <img src={menu} width={"30px"} />
            </button>
            <div className={show_desktop ? classes.sidebarDesk_active : classes.sidebarDesk}>
                <div className={classes.SidebarItemsHolder}>
                    <SidebarItems getBack={getBackHandler} data={props.data}/>
                </div>
            </div>

            {/* mobile */}
            <div className={classes.sidebarMobile}>
                <button className={classes.menuBtnMobile} onClick={handleShow}>
                    <img src={menu} width={"30px"} />
                </button>
                <Offcanvas show={show} onHide={handleClose} className="yellowBg">
                    <Offcanvas.Header closeButton />
                    <Offcanvas.Body>
                        <div className="menuItemMobile">
                            <SidebarItems getBack={getBackHandler} data={props.data} />
                        </div>
                    </Offcanvas.Body>
                </Offcanvas>
            </div>

        </>
    )
}

export default Sidebar;