import React from "react";

import classes from "./certificates.module.css";
import pic from "./5.jpg"


function Certificates(props) {
    return(
        <>
            <div className={classes.background}>
                <div className={`${classes.contentHolder} bg-white p-3`}>
                        <div className={classes.scroll}>
                            <div className={`d-flex flex-column align-items-center ${classes.header}`}>
                                <span className={`${classes.title} text-center`}>
                                    {`Courses & Certificates`}
                                </span>
                                <div className={classes.imgHolder}>
                                    <img src={pic} width="100%"/>
                                </div>
                            </div>

                            {/* <!-- DESKTOP --> */}
                            <div className={`d-flex justify-content-around mt-3 ${classes.headerTable}`}>
                                <span>
                                    Courses
                                </span>
                                <span>
                                    Institute
                                </span>
                            </div>
                            <div className={classes.tableContent}>
                                <div className={classes.webCer}>
                                    <a href={`https://www.alison.com/user/learner-verification/22348880/3874`} className={classes.link}>
                                        {`CSS & JavaScript for Fully Responsive`}
                                    </a>
                                </div>
                                <div className={`${classes.webCer} ${classes.institute}`}>
                                    Alison Online School
                                </div>

                                <div className={classes.uiCer}>
                                    <a href={`https://www.coursera.org/verify/WDWP2KMXKSZZ`} className={classes.link}>
                                        Visual Elements of UI
                                    </a>
                                </div>
                                <div className={`${classes.uiCer} ${classes.institute}`}>
                                    CalArts
                                </div>

                                <div className={classes.bizCer}>
                                    <a href={`https://www.coursera.org/verify/R8H3N3DP743M`} className={classes.link}>
                                        English in Business
                                    </a>
                                </div>
                                <div className={`${classes.bizCer} ${classes.institute}`}>
                                    Washington Uni
                                </div>
                            </div>
                            
                        </div>
                </div>
            </div>
        
        </>
    )
}

export default Certificates;