import React, {useState} from "react";

import "./App.css";
import AboutMe from "./sections/aboutMe/AboutMe";
import Certificates from "./sections/certificate/Certificate";
import ContactMe from "./sections/contactMe/ContactMe";
import Expriences from "./sections/exprience/Exprience";
import Skills from "./sections/skills/Skills"
import Welcome from "./sections/welcome/Welcome";
import Sidebar from "./sections/sidebarMenu/Sidebar";

function App(props) {
    const [state, setState] = useState({
        welcome : {selected: true, bg: null, name: "Welcome", component: <Welcome/>},
        aboutMe : {selected: false, bg: null, name: "About Me", component: <AboutMe />},
        skills : {selected: false, bg: null, name: "Skills", component: <Skills/>},
        certificates : {selected: false, bg: null, name: "Certificates", component: <Certificates/>},
        expriences : {selected: false, bg: null, name: "Expriences", component: <Expriences/>},
        contactMe : {selected: false, bg: null, name: "Contact Me", component: <ContactMe/>},
    })

    const [toShow, setShow] = useState(state.welcome.component)

    const getBackHandler = e => {
        setState(e);

        Object.entries(e).map(item => {
            if(item[1].selected) {
                setShow(item[1].component)
            }
        })
    }

    return (
        <>
            <Sidebar getBack={getBackHandler} data={state} />

            <div>
                {
                    toShow
                }

            </div>
        </>
    )
}

export default App;