import React from "react";

import classes from "./about.module.css";
import aboutMe from "./about-me.jpg";


function AboutMe(props) {
    return(
        <>
            <div className={classes.background}>
                <div className={classes.contentHolder}>
                    <div className={`d-flex flex-column ${classes.aboutMeHolder}`}>
                        <div className={`d-flex justify-content-around align-items-center ${classes.aboutMeHeader}`}>
                            <div className={classes.imgHolder}>
                                <img src={aboutMe} />
                            </div>
                            <span className={classes.whoAmI}>
                                Who Am I ?
                            </span>
                        </div>
                        <p className={`mt-3 ${classes.descr}`}>
                            <span className="d-block">
                                Meticulous and detail-oriented Front End Developer with demonstrable knowledge and skills in HTML, CSS, JavaScript and React.
                            </span>
                            <span className="d-block">
                                Fast Learner and delight to {`R&D`} new concepts and technologies in work.
                            </span>
                            <span className="d-block">
                                Determined in time management and able to monitor the projects through Azure or Agile. Also confidant in global communnication in an international environment.
                            </span>
                        </p>
                    </div>
                </div>
            </div>
        </>
    )    
}

export default AboutMe;