import React, { useState } from "react";

import classes from "./sidebarItem.module.css";

function SidebarItems(props) {
    const [state, setState] = useState( props.data );

    const clickHandler = item => {
        const sample = state;

        // make all false
        Object.keys(sample).map(item => {
            sample[item].selected = false;
        })

        // make the clicked one true
        sample[item[0]].selected = true;

        // set
        setState(sample);
        props.getBack(state, {selected: true});
    }

    return(
        <>
            <div className="d-flex flex-column">
                    {
                        Object.entries(state).map( (item, index) => 
                            <button 
                                className={item[1].selected ? classes.eachItem_active : classes.eachItem} 
                                key={index} 
                                onClick={() => clickHandler(item)}
                            >
                                <span className={classes.eachItemSpan}>
                                    {item[1].name}
                                </span>
                            </button>
                        )
                    }
            </div>
        </>
    )
}

export default SidebarItems;