import React from "react";

// packages
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, Pagination, A11y } from 'swiper';

// css
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import "./main.css";
import classes from "./skillsItem.module.css";

// img
import html from "./icons/html.png";
import css from "./icons/css.png";
import js from "./icons/js.png";
import reactBootstrap from "./icons/bootstrap.png";
import sass from "./icons/sass.png";
import materialUI from "./icons/material.png";
import react from "./icons/react.jpg";
import redux from "./icons/redux.png";
import axios from "./icons/axios.png";
import webpack from "./icons/webpack.png";
import jest from "./icons/jest.png";
import ui from "./icons/ui.png";
import docker from "./icons/doc.png";
import node from "./icons/node.png";
import mysql from "./icons/mysql.png";
import farsi from "./icons/fa.png";
import english from "./icons/eng.png";


function SkillsItem(props) {

    const viewPort = window.matchMedia("(max-width: 660px)").matches;
    
    return(
        <div className={classes.wrapAll}>
            <div class="d-flex justify-content-center pb-3">
                <span class={classes.title}>
                    {`Skills & Knowledge`}
                </span>
            </div>

            <Swiper 
                spaceBetween={50}
                modules={[Navigation, Pagination, A11y]}
                slidesPerView={1}
                navigation= {!viewPort}
                pagination={{clickable: true}}
                // onSlideChange={() => console.log('slide change')}
                // onSwiper={(swiper) => console.log(swiper)}

            >
                <SwiperSlide>
                    <div className={classes.eachSliderBox}>
                        <span className={classes.SlideTitle}>
                            Front End Tringle       
                        </span>

                        <div className={classes.relatedArea}>
                            <div className="d-flex align-items-center">
                                <div className={classes.skillsIcon}>
                                    <img src={html} />
                                </div>
                                <span>
                                    HTML
                                </span>
                            </div>

                            <div className="mt-3 d-flex align-items-center">
                                <div className={classes.skillsIcon}>
                                    <img src={css} />
                                </div>
                                <span>
                                    CSS
                                </span>
                            </div>

                            <div className="mt-3 d-flex align-items-center">
                                <div className={classes.skillsIcon}>
                                    <img src={js} />
                                </div>
                                <span>
                                   JavaScript
                                </span>
                            </div>
                            
                        </div>
                    </div>
                </SwiperSlide>

                
                <SwiperSlide>
                    <div className={classes.eachSliderBox}>
                        <span className={classes.SlideTitle}>
                            UI Nature        
                        </span>

                        <div className={classes.relatedArea}>
                            <div className="d-flex align-items-center">
                                <div className={classes.skillsIcon}>
                                    <img src={reactBootstrap} />
                                </div>
                                <span>
                                    React Bootstrap
                                </span>
                            </div>

                            <div className="mt-3 d-flex align-items-center">
                                <div className={classes.skillsIcon}>
                                    <img src={sass} />
                                </div>
                                <span>
                                    Sass
                                </span>
                            </div>

                            <div className="mt-3 d-flex align-items-center">
                                <div className={classes.skillsIcon}>
                                    <img src={materialUI} />
                                </div>
                                <span>
                                    Material UI
                                </span>
                            </div>

                        </div>
                    </div>
                </SwiperSlide>

                <SwiperSlide>
                    <div className={classes.eachSliderBox}>
                        <span className={classes.SlideTitle}>
                            JS Nature        
                        </span>

                        <div className={classes.relatedArea}>
                            <div className="d-flex align-items-center">
                                <div className={classes.skillsIcon}>
                                    <img src={react} />
                                </div>
                                <span>
                                    React
                                </span>
                            </div>

                            <div className="mt-2 d-flex align-items-center">
                                <div className={classes.skillsIcon}>
                                    <img src={redux} />
                                </div>
                                <span>
                                    Redux
                                </span>
                            </div>

                            <div className="mt-2 d-flex align-items-center">
                                <div className={classes.skillsIcon}>
                                    <img src={webpack} />
                                </div>
                                <span>
                                    Webpack
                                </span>
                            </div>

                            <div className="mt-2 d-flex align-items-center">
                                <div className={classes.skillsIcon}>
                                    <img src={axios} />
                                </div>
                                <span>
                                    Axios
                                </span>
                            </div>
                        </div>
                    </div>
                </SwiperSlide>

                <SwiperSlide>
                    <div className={classes.eachSliderBox}>
                        <span className={classes.SlideTitle}>
                            Related Area        
                        </span>

                        <div className={classes.relatedArea}>
                            <div className="d-flex align-items-center">
                                <div className={classes.skillsIcon}>
                                    <img src={ui} />
                                </div>
                                <span>
                                    UI/UX
                                </span>
                            </div>

                            <div className="mt-2 d-flex align-items-center">
                                <div className={classes.skillsIcon}>
                                    <img src={node} />
                                </div>
                                <span>
                                    Node Js
                                </span>
                            </div>

                            <div className="mt-2 d-flex align-items-center">
                                <div className={classes.skillsIcon}>
                                    <img src={mysql} />
                                </div>
                                <span>
                                    MySQL
                                </span>
                            </div>

                            <div className="mt-2 d-flex align-items-center">
                                <div className={classes.skillsIcon}>
                                    <img src={docker} />
                                </div>
                                <span>
                                    Docker
                                </span>
                            </div>
                        </div>
                    </div>
                </SwiperSlide>
                
                <SwiperSlide>
                    <div className={classes.eachSliderBox}>
                        <span className={classes.SlideTitle}>
                            Language       
                        </span>

                        <div className={classes.relatedArea}>
                            
                            <div className="mt-2 d-flex align-items-center">
                                <div className={classes.skillsIcon}>
                                    <img src={english} />
                                </div>
                                <span>
                                    {`English (Fluent)`}
                                </span>
                            </div>
                            
                            <div className="mt-2 d-flex align-items-center">
                                <div className={classes.skillsIcon}>
                                    <img src={farsi} />
                                </div>
                                <span>
                                    {`Persian (Native)`}
                                </span>
                            </div>
                        </div>
                    </div>
                </SwiperSlide>
            </Swiper>
        </div>
    )
}


export default SkillsItem;