import React from "react";

import classes from "./expriences.module.css";

function Expriences(props) {
    return(
        <>
            <div className={classes.background}>
                <div className={classes.contentHolder}>
                    <div className={`bg-white ${classes.expriencesHolder}`}>
                        <div className="d-flex justify-content-center pb-3">
                            <span className={classes.title}>
                                Expriences
                            </span>
                        </div>

                        <div className="my-3">
                            <span className={`d-block ${classes.workTitle}`}>
                                Web Deisgner
                            </span>
                            <span className={`d-block ${classes.workDetail}`}>
                                Vocational Training - Tehran Institute of Technology
                            </span>
                            <div className={classes.desc}>
                                <span className="d-block">
                                    Vocational training intenrnship in Front End Development.
                                </span>
                                <span>
                                    Exprience in the relevent courseworks such as OOP Programming, Design Pattern, SOLID, Ajax with RESTful API, Dockerizing Front End App, and understanding of how the Back End works.
                                </span>
                            </div>
                        </div>

                        <div>
                            <span className={`d-block ${classes.workTitle}`}>
                                Front End Developer
                            </span>
                            <span className={`d-block ${classes.workDetail}`}>
                                Full Time - Akamnet Company 
                            </span>
                            <div className={classes.desc}>
                                <span className="d-block">
                                    Lunch websites or web applications for the new businesses with React or Vanilla JavaScript.
                                </span>
                                <span className="d-block">
                                    Refactor the non standard websites for the startups.
                                </span>
                                <span className="d-block">
                                    Build company's own CMS arcitechture by Micro Front End.
                                </span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Expriences;