import React from "react";

import classes from "./contact.module.css";

function ContactMe(props) {
    return(
        <>
            <div className={classes.background}>
                <div className={classes.contentHolder}>
                    <div className="p-5 bg-white">
                        <div className="d-flex flex-column align-items-center justify-content-center pb-3">
                            <span class={classes.title}>
                                Contact Me
                            </span>
                            
                            <div className="card-body d-flex flex-column">
                                <a href={`https://www.linkedin.com/in/ali-lamiry-13a8a1219/`} class="btn btn-outline-secondary mt-5">
                                    <span>
                                        Linkedin
                                    </span>
                                    <i className="lab la-linkedin h4 text-light"></i>
                                </a>
                                <a href={`ali.nabizadeh79@yahoo.com`} className="btn btn-outline-warning mt-5">
                                    <span>
                                        Email
                                    </span>
                                    <i className="lab la-github h4 text-light"></i>
                                </a>
                                <a href={`https://www.github.com/NabizadehAli-79`} className={`btn ${classes.btnOutlinePurple} mt-5`}>
                                    <span>
                                        Github
                                    </span>
                                    <i className="lab la-github h4 text-light"></i>
                                </a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        
        </>
    )
}

export default ContactMe;